import React, {useEffect, useState} from 'react';
import { Box, Button, Stack, TextField, Typography, typography } from '@mui/material'; 
import {exerciseOptions,fetchData } from '../utils/fetchData';
import HorizontalScrollbar from './HorizontalScrollbar';


const SearchExercises = ({setExercises, bodyPart, setBodyPart}) => {
  //usestate hook
const [search, setSearch] = useState('')
// const [exercises, setExercises] = useState([]);
const [bodyParts,setBodyParts] = useState([])
 
useEffect(() => {
  const fetchExercisesData = async () => {
    const bodyPartsData = await fetchData('https://exercisedb.p.rapidapi.com/exercises/bodyPartList', exerciseOptions);
    setBodyParts(['all',...bodyPartsData]);
  }
}, [])

const handleSearch= async() =>{
  if(search){
     
    const exerciseData = await fetchData('https://exercisedb.p.rapidapi.com/exercises', exerciseOptions);
    const SearchExercises = exerciseData.filter(
      (exercise)=> exercise.name.toLowerCase().includes(search)
      || exercise.target.toLowerCase().includes(search)
      || exercise.equipment.toLowerCase().includes(search)
      || exercise.bodyPart.toLowerCase().includes(search)
    );
    setSearch('');
    setExercises(SearchExercises);
  }
}

  return (
    <Stack alignItems='center' mt='37px' jutifycontent='center' p='20px'>
      <Typography fontSize={700} sx={{
        fontSize:{lg:'44px',xs:'30px'}
      }} mb='50px' textAlign='center'>
         Awesome Exercises You <br/>
         Should Know
      </Typography>
      <Box position='relative' mb='30px'>
        <TextField
          height='76px'
          value={search}
           onChange={(e)=>setSearch(e.target.value.toLowerCase())}
           placeholder='Search Exercises'
           type='text'
           sx={{
            input:{
              fontWeight:'700',
              border:'none',
              borderRadius:'4px'
            },
            width:{lg:'800px', xs:'350px'}
            ,
            backgroundColor:'#fff',
            borderRadius:'40px'
           }}

        />
        <Button className='search-btn'
          sx={{
             bgcolor:'#ff2625',
             color:'#fff',
             textTransform:'none',
             width:{lg:'175px', xs:'80px'},
             fontSize:{lg:'20px',xs:'14px'},
             height:'56px',
             position:'absolute'
             , right:'0'
          }}
          onClick={handleSearch}
        >
          Search
        </Button>
      </Box>
      <Box sx={{
        position:'relative', width:'100%', p:'20px'
      }}>
        <HorizontalScrollbar 
          data={bodyParts} bodyPart={bodyPart} setBodyPart={setBodyPart} isBodyParts
        />
      </Box>
    </Stack>
    )
}

export default SearchExercises